<style >
.block.doshow{
  animation: doshow .8s ease .2s both;
}
.block.dohide{
  animation: dohide .8s ease .2s both;
}
.block.doshow .home-left{
  animation: leftin .8s ease .2s both;
}
.block.dohide .home-left{
  animation: leftout .8s ease .2s both;
}
.block.doshow .home-top{
  animation: topin .8s ease .2s both;
}
.block.doshow .home-bottom{
  animation: bottomin .8s ease .2s both;
}
.block.doshow .home-right{
  animation: rightin .8s ease .2s both;
}
.block.dohide .home-top{
  animation: topout .8s ease .2s both;
}
.block.dohide .home-bottom{
  animation: bottomout .8s ease .2s both;
}
.block.dohide .home-right{
  animation: rightout .8s ease .2s both;
}
.block.doshow .home-bottom-right{
  animation: bottomin .8s ease .2s both;
}
.block.dohide .home-bottom-right{
  animation: rightout .8s ease .2s both;
}
@keyframes leftin{
  0% {
    left: -10%;
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes leftout{
  0% {
    opacity: 1;
  }
  100% {
    left: -10%;
    opacity: 0;
  }
}
@keyframes topin{
  0% {
    top: -100%;
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes bottomin{
  0% {
    top: 150%;
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes rightin{
  0% {
    left: 100%;
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes topout{
  0% {
    opacity: 1;
