  computed: {
    getImg1(){
      let url = '';
      if(this.workTrigger){
        if(this.triggerWorking){
          url = this.workList[this.currentWorkCity][this.currentWorkIdx+1].img;
        }else{
          url = this.workList[this.currentWorkCity][this.currentWorkIdx].img+'-act';
        }
      }else{
        if(this.triggerWorking){
          url = this.workList[this.currentWorkCity][this.currentWorkIdx].img+'-act';
        }else{
          let idx = this.currentWorkIdx<this.workList[this.currentWorkCity].length-1?this.currentWorkIdx+1:0;
          url = this.workList[this.currentWorkCity][idx].img;
        }
      }
      return url;
    },
    getImg2(){
      let url = '';
      if(!this.workTrigger){
        if(this.triggerWorking){
          let idx = this.currentWorkIdx<this.workList[this.currentWorkCity].length-1?this.currentWorkIdx+1:0;
          url = this.workList[this.currentWorkCity][idx].img;
        }else{
          url = this.workList[this.currentWorkCity][this.currentWorkIdx].img+'-act';
        }
      }else{
        if(this.triggerWorking){
          url = this.workList[this.currentWorkCity][this.currentWorkIdx].img+'-act';
        }else{
          let idx = this.currentWorkIdx<this.workList[this.currentWorkCity].length-1?this.currentWorkIdx+1:0;
          url = this.workList[this.currentWorkCity][idx].img;
        }
      }
      return url;
    }
  },
  mounted(){
    let that = this;
    setTimeout(() => {
      that.tabChange("home");
    }, 1000);
    
    $('.daiyuAN').on('mouseover',function(e){
      $(this).addClass('tanchuRShow');
      $(this).removeClass('tanchuRHide');
    })
    $('.daiyuAN').on('mouseout',function(e){
      $(this).addClass('tanchuRHide');
      $(this).removeClass('tanchuRShow');
    })
    $('.contentBody').on('click',function(e) {
      let els = $('.btn-box');
      for (let i = 0; i < els.length; i++) {
        $(els[i]).removeClass('act');
      }
    });
  },
  methods: {
    showList(id){
      let els = $('.btn-box');
      for (let i = 0; i < els.length; i++) {
        $(els[i]).removeClass('act');
      }
      $('.btn-box.mybtn'+id).addClass('act');
    },
    tabChange(menu){//头部导航
      if(this.currentMenu!=menu){
        this.currentMenu = menu;
        this.$refs.carouselRef.setActiveItem(menu);
        if(menu=="product"){
          this.currentProductTabIdx = 0;
          this.currentProductModelIdx = 0;
        }
      }
    },
    homeChange(type){
      if(type==-1){
        if(this.currentHomeTabIdx==0){
          this.currentHomeTabIdx = 3;
        }else{
          this.currentHomeTabIdx--;
        }
      }else{
        if(this.currentHomeTabIdx==3){
          this.currentHomeTabIdx = 0;
        }else{
          this.currentHomeTabIdx++;
        }
      }
    },
    homeBtn(type){
      switch (type) {
        case 1:
          this.currentMenu = "product";
          this.$refs.carouselRef.setActiveItem(this.currentMenu);
          this.currentProductIdx = 0;
          this.currentProductModelIdx = 0;
          this.currentProductTabIdx = 0;
          break;
        case 2:
          this.currentMenu = "product";
          this.$refs.carouselRef.setActiveItem(this.currentMenu);
          this.currentProductIdx = 1;
          this.currentProductModelIdx = 0;
          this.currentProductTabIdx = 0;
          break;
        case 3:
          this.currentMenu = "product";
          this.$refs.carouselRef.setActiveItem(this.currentMenu);
          this.currentProductIdx = 0;
          this.currentProductModelIdx = 1;
          this.currentProductTabIdx = 0;
          break;
        case 4:
          this.currentMenu = "work";
          this.$refs.carouselRef.setActiveItem(this.currentMenu);
          break;
        default:
          break;
      }
    },
    homeTabChange(idx){
      this.currentHomeTabIdx = idx;
    },
    companyTabChange(idx){
      this.currentCompanyTabIdx = idx;
    },
    triggerWork(type,idx){
      let that = this;
      if(type=='img1'&&this.workTrigger||type=='img2'&&!this.workTrigger)return;//点击灰色的才有切换效果
      if(that.triggerWorking)return;
      that.triggerWorking = true;
      if(typeof idx != 'undefined'){
        that.workTrigger = idx%2==1?false:true;
      }else{
        that.workTrigger = !that.workTrigger;
      }
      setTimeout(() => {
        if(type=='quick'){
          that.currentWorkIdx = idx;
        }else{
          if(that.currentWorkIdx==that.workList[that.currentWorkCity].length-1){
            that.currentWorkIdx = 0;
          }else{
            that.currentWorkIdx++;
          }
        }
        that.triggerWorking = false;
      }, 900);
    },
    triggerWorkConcat(type,val){
      let that = this;
      if(type=='img1'&&this.workTriggerConcat||type=='img2'&&!this.workTriggerConcat)return;//点击灰色的才有切换效果
      if(that.triggerConcating)return;
      that.triggerConcating = true;
      that.workTriggerConcat = !that.workTriggerConcat;
      setTimeout(() => {
        that.currentConcatCity = that.currentConcatCity=='深圳'?'重庆':'深圳';
        that.triggerConcating = false;
      }, 900);
    },
    productTabChange(idx){
      this.currentProductTabIdx = idx;
    },
    toBOSS(){
      window.open('https://www.zhipin.com/gongsi/407dd3d30385323003Fy39q8Eg~~.html?ka=company-intro');
    }
  }
}
</script>


