<template>
  <div class="contentBox">
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  mounted  () {
  	let that = this;
  	setTimeout(function(){
  		that.setMaxWidth();
  	},1000);
    window.οnresize=function(){
      that.setMaxWidth();
    }
  },
  methods: {
  	setMaxWidth(){
  		let box = document.getElementsByClassName("contentBody")[0];
  		//let box = document.body;
  		if(box)box.style.maxWidth = (window.innerWidth*window.devicePixelRatio)+"px";
  	}
  }
}
</script>

<style>
  html,body{
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
  }
  .contentBox {
    min-width: 1200px;
    position: relative;
    width: 100%;
    height: 100%;
    /*overflow-x: auto;*/
    overflow: hidden;
    margin: 0;
    padding: 0;
    color: #000;
  }
  .contentBody{
    position: relative;
    width: 100%;
    /*overflow-x: hidden;
    overflow-y: scroll;*/
    /*-webkit-overflow-scrolling: touch;*/
  }
</style>



