花豹科技有限公司成立于2019年，我们定位国内IOT智能设备和超低功耗系统市场，开发BAOS。我们不仅拥有自己的独立品牌，也会与其他厂商合作，推出基于BAOS的应用产品。\n            ")]),_vm._v(" "),_c('div',{staticClass:"ct-info-30h"},[_vm._v("\n              基于花豹科技的BAOS，IOT智能设备可实现无需安装、升级；同时能够帮助过于臃肿的客户端瘦身。未来，我们推出的开放的技术体系可以降低开发者的开发难度。对超低功耗设备而言，我们也能提供语音识别、消息推送系统OTA等一系列功能。而在成本方面，BAOS能降低成本到同类产品的一半。\n            ")]),_vm._v(" "),_c('div',{staticClass:"ct-info-30h"},[_vm._v("\n              我们的创始团队均由小米、华为、OPPO等企业的资深工程师和设计师组建，创始人吴廷彬是小米科技第32号创始员工，搭建了小米的用户系统，主导了小米所有服务和客户端授权体系的建立，也在小米生态链内孵化了车载智能生态链以及智能门锁等项目。\n            ")])]),_vm._v(" "),_c('div',{staticClass:"ct-box"},[_c('div',{staticClass:"ct-title"},[_vm._v("BAOS Key Features")]),_vm._v(" "),_c('div',{staticClass:"ct-info-30h"},[_vm._v("\n              BT网络支持、语音交互界面、图形交互界面、小程序OS易扩展、资源利用极度优化\n            ")])])]),_vm._v(" "),_c('div',{directives:[{name:"show",rawName:"v-show",value:(_vm.currentCompanyTabIdx==1),expression:"currentCompanyTabIdx==1"}],staticClass:"fun-ct"},[_c('div',{staticClass:"ct-box"},[_c('div',{staticClass:"ct-title"},[_vm._v("智能穿戴[类Android]系统")]),_vm._v(" "),_c('div',{staticClass:"ct-info-40h",staticStyle:{"margin-top":"30px"}},[_vm._v("\n              1. 5G+ Cloud带来无与伦比的网路连接性，所有服务都应该触手可及，按需访问。\n            ")]),_vm._v(" "),_c('div',{staticClass:"ct-info-40h"},[_vm._v("\n              2. 不需要App的下载/升级/卸载才能使用，我们可以看到微信小程序已经证明这个是趋势。\n            ")]),_vm._v(" "),_c('div',{staticClass:"ct-info-40h"},[_vm._v("\n              3. OS的大部分需要的计算资源都通过cloud来实现，对client side的硬件要求降低，可以极大的降低设备成本。\n            ")]),_vm._v(" "),_c('div',{staticClass:"ct-info-40h"},[_vm._v("\n              4. 市场上还没有一个标准的Cloud Based RTOS（适用与超低功耗的小系统），现在设备的联网能力仍然是每个厂商自己来研发。\n            ")]),_vm._v(" "),_c('div',{staticClass:"ct-info-40h"},[_vm._v("\n              5. IOT设备和互联网服务的融合还没有开始。我们要作为服务提供方，提供一系列能力来帮助设备和互联网服务互相融合。\n            ")]),_vm._v(" "),_c('div',{staticClass:"ct-info-40h"},[_vm._v("\n              6. 中美贸易战带来整个世界脱钩的可能性，中国市场需要独立的OS以及OS上面的基础服务提供商来服务好中国的硬件品牌商。\n            ")])]),_vm._v(" "),_c('div',{staticCl